.home-main-container{
	width: 100%;
	height:100%;
	box-sizing: border-box;
	position: relative;
	overflow: hidden;
}

.home-inner-container{
position: absolute;
width: 100%;
height: 75%;
margin: auto;
top:0;
bottom: 0;
}


.home-main-div-grid{
  width: 100%;
  height:50%;	
  display: grid;
  grid-template-columns: 30% 70%;
  grid-template-rows: 100% ;
  background-color: inherit;
 
}


.home-white-block-animated{
height: 100%;
width: 100%;
background-color: white;
margin: 0;
padding: 0;
-webkit-animation-name:block;
        animation-name:block;
-webkit-animation-duration: 1.2s;
        animation-duration: 1.2s;
-webkit-animation-delay: 1s;
        animation-delay: 1s;
-webkit-animation-fill-mode:both;
        animation-fill-mode:both;
}


.home-logo-animated{

width: 100%;
height:100%;
padding: 0;
margin: 0;
display: none;
}

.shamir{
position: fixed;
width: 75vw;
right: -4em;

}


.home-text-div{
	height: auto;
	width: 35%;
	direction: rtl;
	right: 12em;
	display: block;
	position: absolute;

    overflow: hidden;
    text-overflow: clip;
  	-webkit-animation-name:text;
  	        animation-name:text;
	-webkit-animation-duration: 1.3s;
	        animation-duration: 1.3s;
	-webkit-animation-delay: 1.2s;
	        animation-delay: 1.2s;
	-webkit-animation-fill-mode:both;
	        animation-fill-mode:both;
    margin: 0.6em; 
}


.home-text-div div{

	

	float: left;
	direction: ltr;
	-webkit-animation-name:fadein;
	        animation-name:fadein;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-delay: 2s;
	        animation-delay: 2s;
	-webkit-animation-fill-mode:both;
	        animation-fill-mode:both;
}

.home-text-div p{
	color:white;
	font-size:1.5em;
	direction: rtl;
	font-weight: 300;
	font-family: 'Assistant';



}

.home-image-div{
	position: absolute;
	left:9em;
	top:-3em;
	display: block;
	width: 30vw;
	height: 75vh;
	z-index: 2;
	background-size: contain;
	background-repeat: no-repeat;
  	-webkit-animation-name:trasactionx,floating;
  	        animation-name:trasactionx,floating;
	-webkit-animation-duration: 1s,3s;
	        animation-duration: 1s,3s;
	-webkit-animation-delay: 1.4s,3s;
	        animation-delay: 1.4s,3s;
	-webkit-animation-fill-mode:both,none;
	        animation-fill-mode:both,none;
	-webkit-animation-iteration-count: 1, infinite;
	        animation-iteration-count: 1, infinite;
	-webkit-animation-timing-function:ease,ease-in-out;
	        animation-timing-function:ease,ease-in-out;
}

/* The animation code */
@-webkit-keyframes block {
  from {width: 0%;}
  to {width: 100%;}
}
@keyframes block {
  from {width: 0%;}
  to {width: 100%;}
}

@-webkit-keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@-webkit-keyframes text {
  from {height : 0%;}
  to {height: 70%;}
}

@keyframes text {
  from {height : 0%;}
  to {height: 70%;}
}


@-webkit-keyframes trasactionx {
  from {left : -40em;}
  to {left: 9em;}
}


@keyframes trasactionx {
  from {left : -40em;}
  to {left: 9em;}
}


@-webkit-keyframes move{
  from {left : 9em;right:0em;top:-3em;bottom: 0em}
  to {left : 9.1em;right:0.1em;top:-2.9em;bottom: 0.1em}
}


@keyframes move{
  from {left : 9em;right:0em;top:-3em;bottom: 0em}
  to {left : 9.1em;right:0.1em;top:-2.9em;bottom: 0.1em}
}


@-webkit-keyframes floating {
    from { transform: translate(0,  0px); }
    65%  { transform: translate(0, 5px); }
    to   { transform: translate(0, -0px); }    
}


@keyframes floating {
    from { transform: translate(0,  0px); }
    65%  { transform: translate(0, 5px); }
    to   { transform: translate(0, -0px); }    
}




