  from {opacity: 0;}
  to {opacity: 1;}
}

@-webkit-keyframes movein {
  from {left: -100em;}
  to {left: 0;}
}

@keyframes movein {
  from {left: -100em;}
  to {left: 0;}
}

@-webkit-keyframes moveinimg {
  from {left: 100em;}
  to {left: -1em;}
}

@keyframes moveinimg {
  from {left: 100em;}
  to {left: -1em;}
}

.grid_about{
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns:0.3fr 0.9fr 1fr;
  grid-template-rows: 100%;
  
  grid-gap: 3em;
  margin-top: 0em;
}

.about_animated_block{

	margin:0;
	padding: 0;
	position: relative;
	width: 100%;
	height: 100%;

	

}

.about_animated_block div{
	background-color: white;
	height: 35%;
	width: 100%;
	position: absolute;
	top: 3em;
	-webkit-animation: movein 1s 0s;
	        animation: movein 1s 0s;
	
}


.about_animated_block img{
 	position: absolute;
	width: 40vw;
	height: auto;
	z-index: 1;
	top:4em;
	left:-1em;
	-webkit-animation:moveinimg 1s .5s both;
	        animation:moveinimg 1s .5s both;
  	-webkit-animation-name:moveinimg,floating;
  	        animation-name:moveinimg,floating;
	-webkit-animation-duration: 1s,3s;
	        animation-duration: 1s,3s;
	-webkit-animation-delay: 0.5s,3s;
	        animation-delay: 0.5s,3s;
	-webkit-animation-fill-mode:both,none;
	        animation-fill-mode:both,none;
	-webkit-animation-iteration-count: 1, infinite;
	        animation-iteration-count: 1, infinite;
	-webkit-animation-timing-function:ease,ease-in-out;
	        animation-timing-function:ease,ease-in-out;

}




