	.animated-title-projects {
		position: absolute;
		top:-14em;
		left:8em;
		width: 30vw;
		transform: rotate(90deg);
	}
	
	.projects-inner-container{
		height: 40%;
		grid-gap: 2em;	
	}
	
	.upper-div{
		margin-left:.3em;
	}
	
	.inner-upper-div{
		width: 95vw;
		padding-right:35em;
		padding-left: -1em;
	}	

	.bottom-div{
		width: 100%;
	}
		
	.inner-bottom-div{
		width: 95vw;
		padding-right: 1em;
	}
		

		
	.bottom-div-animation{
		width: 10vw;
		right:1em;
	}
}

