
.box{
	width: 10vmax;
	height: 100%;
	display: block;
	position: relative;
}


.box img{
	max-width: 100%;
	height: auto;
	position: absolute;
	top:0;
	bottom: 0;
	left:0;
	right: 0;
	margin: auto;

}


@media screen and (max-width: 480px) {


	.box{
		width: 12vmax;
	}
	


}
